html {
    box-sizing: border-box;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    margin: 0;
}

body,
input {
    /*font-family: "Lucida Console", Monaco, monospace;*/
    font-family: 'IBM Plex Mono', monospace;
    font-size: 16px;
    font-weight: 400;
}

.sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    border: 0;
}

.d-flex {
    display: -ms-flexbox !important;
    display: flex !important;
}